.header_txt_space{
    line-height: 1em;
    position: relative;
    margin-left: -20px;
    margin-right: auto;
    margin-top: 40px;
    margin-bottom: 5px;
    width: 100%;
    background: rgb(32, 61, 77);
    padding: 5px  20px 5px 20px;
    font-family: DejaVu Sans Mono, monospace;
    color: rgb(214, 225, 231);
    font-size: 30px;
    font-stretch: ultra-expanded;
}

.txt_space{
    line-height: 1.5em;
    position: relative;
    margin-left: -20px;
    margin-right: auto;
    width: 100%;
    background: transparent;
    padding: 5px  20px 5px 20px;
    font-family: DejaVu Sans Mono, monospace;
    color: rgb(214, 225, 231);
    font-size: 20px;
    font-stretch: ultra-expanded;
    word-wrap: break-word;
}