a{
    background: transparent;  
    color: rgb(222, 235, 235);
}

.left_space{
    max-width: 100%;
    position: absolute;
    margin: 0px;
    top: 0px;
    left: 0px;
    width: 28.5%;
    height: 1200px;
    padding: 20px;
    background: rgb(55, 103, 129); 
    color: rgb(222, 235, 235);
    border-radius: 5px 0px 0px 5px;
}

.right_space{
    max-width: 100%;
    position: relative;
    margin: 40px;
    margin-right: auto;
    margin-left: auto;
    width: 60%;
    height: 1200px;
    padding: 20px;
    background: rgb(171, 196, 209);
    color: rgb(222, 235, 235);
    border-radius: 5px;
    box-shadow: 0 0 20px rgba(0,0,0,0.3);
}

.image_main{
    max-width: 80%;
    position: relative;
    top: 27px;
    border-radius: 5px;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
}

.name_space{
    margin-top: 15px;
    margin-bottom: 25px;
    top: 30px;
    position: relative;
    background: transparent;
    font-size: 30px;
    font-family: DejaVu Sans Mono, monospace;
    color: rgb(214, 225, 231);
}