.image_wth_txt_div{
    margin-right: auto;
    margin-left: auto;
    margin: 20px;
    background: transparent;
    font-size: 2vw;
    padding: 15px;
    color: rgb(223, 235, 235);
    word-wrap: break-word;
}

.image_wth_txt_txt{
    position: relative;
    margin-right: auto;
    margin-left: auto;
    background: transparent;
}

.image_wth_txt{
    position: relative;
    max-width: 100%;
    margin: 30px;
    margin-right: auto;
    margin-left: auto;
    background: rgb(35, 37, 37);
    border-radius: 5px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.9);
}

.header_space{
    margin: 40px;
    margin-right: auto;
    margin-left: auto;
    width: 60%;
    padding: 20px;
    background: rgb(55, 103, 129);
    color: rgb(222, 235, 235);
    border-radius: 5px;
    box-shadow: 0 0 20px rgba(0,0,0,0.3);
    font-size: 3vw;
}
