.App {
  text-align: center;
  background-color: rgb(35, 37, 37);

}

::-webkit-scrollbar {
  width: 10px; 
  height: 8px;
  background-color: rgb(35, 37, 37);
}

::-webkit-scrollbar-thumb {
  background-color: rgb(50, 55, 58);
  border-radius: 8px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #333e4d;
}

::-webkit-scrollbar-button:vertical:start:decrement {
  background: linear-gradient(120deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
  linear-gradient(240deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
  linear-gradient(0deg, #02141a 30%, rgba(0, 0, 0, 0) 31%);
  background-color: #f6f8f4;
}

::-webkit-scrollbar-button:vertical:end:increment {
  background:
      linear-gradient(300deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
      linear-gradient(60deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
      linear-gradient(180deg, #02141a 30%, rgba(0, 0, 0, 0) 31%);
  background-color: #f6f8f4;
}

::-webkit-scrollbar-button:horizontal:start:decrement {
  background:
      linear-gradient(30deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
      linear-gradient(150deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
      linear-gradient(270deg, #02141a 30%, rgba(0, 0, 0, 0) 31%);
  background-color: #f6f8f4;
}

::-webkit-scrollbar-button:horizontal:end:increment {
  background:
      linear-gradient(210deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
      linear-gradient(330deg, #02141a 40%, rgba(0, 0, 0, 0) 41%),
      linear-gradient(90deg, #02141a 30%, rgba(0, 0, 0, 0) 31%);
  background-color: #f6f8f4;
}

.navbar_bg{
  z-index: 100;
  background-color: rgb(35, 37, 37);
  width: 100%;
  height: 5.0vw;
  position: sticky;
  margin-left: auto;
  margin-right: auto;
  top: 0px;
  box-shadow: 0 0 0.5vw 0.1vw rgba(35, 37, 37, 1);
}

.navbar{
  font-weight: bold;
  box-shadow: 0 0 1vw rgba(0, 0, 0, 0.9);
  border-radius: 0.2vw;
  position: sticky;
  margin-left: auto;
  margin-right: auto;
  top: 20vw;
  width: 62vw;
  padding: 0.1vw;
  overflow: hidden;
  background-color:  #b6d1db;
  font-size: 1.5vw;
}

.link{
  background-color:  #b6d1db;;
  float: left;
  border-radius: 0.2vw;
  margin: 0.2vw;
  display: block;
  color: #164c61;
  text-align: center;
  padding: 0.8vw 1.5vw;
  text-decoration: none;
}

.link_exit{
  background-color:  #b6d1db;;
  float: right;
  border-radius: 0.2vw;
  margin: 0.2vw;
  display: block;
  color: #164c61;
  text-align: center;
  padding: 0.8vw 1.5vw;
  text-decoration: none;
}

.link:hover {
  background-color: #2d7fa073;
}

.link_exit:hover{
  background-color: #cc636381;
}

.link_active{
  background-color: rgb(55, 103, 129);
  float: left;
  border-radius: 0.2vw;
  margin: 0.2vw;
  display: block;
  color: rgb(215, 225, 231);
  text-align: center;
  padding: 0.8vw 1.5vw;
  text-decoration: none;
}