
.home_page{
    position:relative;
    top: -50px;
    margin-left:auto;
    margin-right:auto;
    font-family:Andale Mono, monospace;
    height: 600px;
    width:1185px;
    background: rgb(55, 103, 129);
    border-radius: 5px;
    box-shadow: 0 0 50px rgba(0,0,0,0.5);
    font-size: 20px;
    font-weight: bold;
}

.home_page_img{
    position:absolute;
    top: 70px;
    left: 50px;
    background-color: transparent;
}

.home_page_txt{
    text-align: center;
    margin: 40px;
    background-color: transparent;
    font-size: 45px;
    color:#c8e0e9;
}

.home_page_btn_my_proj{
    margin-top: 10%;
    margin-left: 35%;
    background: transparent;
}

.home_page_right_space{
    position:relative;
    right:-200px;
    height: 600px;
    width: 300px;
    background-color: #b6d1db;
    border-left: 50px solid transparent;
    -ms-transform: skew(10deg, 0deg);
    -webkit-transform: skew(10deg, 0deg);
    transform: skew(10deg, 0deg);
}

.home_page_right_space1{
    position:relative;
    right:-50px;
    background-color: #b6d1db;
    height: 600px;
    width: 475px;
    border-radius: 0px 5px 5px 0px;
    -ms-transform: skew(-10deg, 0deg);
    -webkit-transform: skew(-10deg, 0deg);
    transform: skew(-10deg, 0deg);
}

.home_page_txt_position{
    width: 400px;
    position: relative;
    top: 190px;
    background-color: transparent;
    font-size: 45px;
    color: rgb(45, 82, 102);
}

